   }\n    }\n\n    &-borderless {\n        border: none;\n\n        &:hover, &:focus, &:disabled {\n            border: none;\n        }\n    }\n\n    &-select-panel {\n        margin-top: 0 !important;\n\n        mat-option {\n            padding-left: 0;\n            padding-right: 0;\n\n            &.mat-option-text {\n                @include form-label;\n                text-align: left;\n            }\n        }\n\n        &.mck-margin-top {\n            margin-top: $spacing-medium !important;\n        }\n    }\n\n    &-checkbox-panel {\n        &.mat-autocomplete-panel.mat-autocomplete-panel {\n            max-height: 278px;\n        }\n\n        mat-option.mck-truncated {\n            & .mat-checkbox-label.mat-checkbox-label {\n                @include max-lines(2);\n            }\n        }\n\n        mat-option {\n            padding-left: 0;\n            padding-right: 0;\n            line-height: 24px;\n            height: unset;\n            min-height: 36px;\n\n            & .mat-checkbox-label.mat-checkbox-label {\n                flex: 1;\n            }\n\n            & .mat-checkbox-label.mat-checkbox-label {\n                @include checkbox-label;\n                text-align: left;\n            }\n\n            &.mck-checkbox-group-header {\n                & .mat-checkbox-label.mat-checkbox-label {\n                    font-weight: 500;\n                }\n            }\n\n            &.mck-checkbox-group-child {\n                margin-left: $spacing-large;\n            }\n\n            mat-checkbox {\n                label.mat-checkbox-layout {\n                    width: 100%;\n                    padding-left: 16px;\n                    padding-right: 16px;\n                }\n\n                div.mat-checkbox-frame.mat-checkbox-frame {\n                    @include mck-checkbox-border;\n                }\n\n                &.mat-checkbox-checked {\n                    div.mat-checkbox-background.mat-checkbox-background {\n                 